<script>
import { defineComponent, computed } from 'vue'
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'
import { useStoreLocation } from 'composable/location'

import { useUser } from 'composable/user'
import Tab from 'bbUi/components/tab/tab.vue'
import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import Input from 'bbUi/components/input/input.vue'
import Select from 'bbUi/components/select/select.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import Pagination from 'components/Pagination.vue'

import copyText from 'utils/copy'

export default defineComponent({
  components: {
    Tab,
    Button,
    Card,
    Input,
    Select,
    Modal,
    Pagination,
    Breadcrumb
  },
  async setup() {
    const store = useStore()
    const route = useRoute()
    const { tab } = useStoreLocation()
    const { me } = useUser()

    const isShowAddLocation = computed(() => {
      return (
        route.matched.find(
          (eachRoute) =>
            eachRoute.name === 'settingManageLocationStoreLocations'
        ) !== undefined
      )
    })

    const breadcrumb = [
      {
        text: 'Settings',
        to: {
          name: 'setting'
        }
      },
      {
        text: 'Manage Locations'
      }
    ]

    return {
      tab,
      me,
      isShowAddLocation,
      store,
      route,
      breadcrumb,
      copyText
    }
  }
})
</script>
